<template>
  <div class="main-content">
    <div class="back-class">
      <el-card class style="flex-grow:1;display:flex;flex-direction: column;">
        <el-input
          v-model="origin"
          type="textarea"
          :maxlenght="-1"
          style="height:100%"
        ></el-input>
      </el-card>
      <div class="flex-btn">
        <div
          style="padding:10px 20px; cursor: pointer;backgroundColor: #ffa200;border-radius: 10px"
          @click="toEncode"
        >加密</div>
        <div style="height:40px"></div>
        <div
          style="padding:10px 20px;cursor: pointer; backgroundColor: #00a2aa;border-radius: 10px"
          @click="toDecode"
        >解密</div>
      </div>
      <el-card style="flex-grow:1;display:flex;flex-direction: column;">
        <el-input v-model="signModel" type="textarea" autosize :maxlenght="-1" style="height:100%"></el-input>
      </el-card>
    </div>
  </div>
</template>


<script>
import { xencode, xdecode } from "@/plugins/sign.js";
export default {
  name: "sign-sign",
  data() {
    return {
      origin: "",
      signModel: ""
    };
  },

  mounted() {},

  methods: {
    toEncode() {
      this.signModel = xencode(this.origin);
    },
    toDecode() {
      try {
        const key = xdecode(this.signModel);
        console.log(key)
        if (typeof key === "object") {
          this.origin = JSON.stringify(key);
        } else {
          this.origin = key;
        }
      } catch (error) {
        window.alert(error.message);
      }
    }
  }
};
</script>


<style>
.main-content{
    flex-grow: 1;
    display: flex;
    flex-direction: column;
}
.back-class {
  display: flex;
  /* height: 100%; */
  padding: 40px;
  flex-grow: 1;
}
.flex-btn {
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  align-items: center;
  width: 100px;
}
.el-card__body {
  height: 100%;
}
.el-textarea__inner {
  height: 100% !important;
}
</style>