function getSign(json = {}) {
	const list = Object.keys(json);
	if(list.length > 1){
		list.sort((j, k) => {
			if (j === k) return 0;
			return j > k ? -1 : 1;
		})
	}
	const result = list.map(item => item + '=' + json[item]);
	if(result.length === 1) return result[0];
	return result.join('&');
}
const ALPHABET = '1234EFGabcdefghijkmnopqrstuvwxyzHJKLMNPQRST56789ABCDUVWXYZ';
const ALPHABET_MAP = {};
const BASE = 58;
for (let i = 0; i < ALPHABET.length; i++) {
	ALPHABET_MAP[ALPHABET.charAt(i)] = i;
}
function ToUTF8(str) {
	const result = [];
	let k = 0;
	for (let i = 0; i < str.length; i++) {
		const j = encodeURI(str[i]);
		if (j.length === 1) {
			// 未转换的字符
			result[k++] = j.charCodeAt(0);
		} else {
			// 转换成%XX形式的字符
			const bytes = j.split("%");
			for (let l = 1; l < bytes.length; l++) {
				result[k++] = parseInt("0x" + bytes[l]);
			}
		}
	}
	return result;
}

function encode(buffer) {
	buffer = ToUTF8(buffer);
	if (buffer.length === 0) return '';
	let i,
		j,
		digits = [0];
	for (i = 0; i < buffer.length; i++) {
		for (j = 0; j < digits.length; j++) {
			// 将数据转为二进制，再位运算右边添8个0，得到的数转二进制
			// 位运算-->相当于 digits[j].toString(2);parseInt(10011100000000,2)
			digits[j] <<= 8;
		}
		digits[0] += buffer[i];
		let carry = 0;
		for (j = 0; j < digits.length; ++j) {
			digits[j] += carry;
			carry = (digits[j] / BASE) | 0;
			digits[j] %= BASE;
		}
		while (carry) {
			digits.push(carry % BASE);
			carry = (carry / BASE) | 0;
		}
	}
	// deal with leading zeros
	for (i = 0; buffer[i] === 0 && i < buffer.length - 1; i++) digits.push(0);
	return digits
		.reverse()
		.map(function(digit) {
			return ALPHABET[digit];
		})
		.join('');
}

function decode(string) {
	if (string.length === 0) return [];
	let i,
		j,
		bytes = [0];
	for (i = 0; i < string.length; i++) {
		const c = string[i];
		// c是不是ALPHABET_MAP的key
		if (!(c in ALPHABET_MAP)) throw new Error('Non-base58 character');
		for (j = 0; j < bytes.length; j++) bytes[j] *= BASE;
		bytes[0] += ALPHABET_MAP[c];
		let carry = 0;
		for (j = 0; j < bytes.length; ++j) {
			bytes[j] += carry;
			carry = bytes[j] >> 8;
			// 0xff --> 11111111
			bytes[j] &= 0xff;
		}
		while (carry) {
			bytes.push(carry & 0xff);
			carry >>= 8;
		}
	}
	// deal with leading zeros
	for (i = 0; string[i] === '1' && i < string.length - 1; i++) bytes.push(0);
	return byteToString(bytes.reverse());
}

function byteToString(arr) {
	if (typeof arr === 'string') {
		return arr;
	}
	let str = '',
		_arr = arr;
	for (let i = 0; i < _arr.length; i++) {
		// 数组中每个数字转为二进制 匹配出开头为1的直到0的字符
		// eg:123-->"1111011"-->{0:"1111",groups: undefined, index: 0, input: "1111011"}
		const one = _arr[i].toString(2),
			v = one.match(/^1+?(?=0)/);
		if (v && one.length == 8) {
			let bytesLength = v[0].length;
			let store = _arr[i].toString(2).slice(7 - bytesLength);
			for (let st = 1; st < bytesLength; st++) {
				store += _arr[st + i].toString(2).slice(2);
			}
			str += String.fromCharCode(parseInt(store, 2));
			i += bytesLength - 1;
		} else {
			str += String.fromCharCode(_arr[i]);
		}
	}
	return str;
}
