/* 加密 */
export const xencode = function(json = "", password = "1") {
	let sign = "";
	if(typeof json === 'string'){
		sign = json;
	} else {
		console.error('json is not String');
		return;
	}
	const result = encode(sign);
	const pass = encode(password);
	if(result.length<pass.length){
		return '';
	}
	const list = [];
	while (list.length < pass.length) {
		const i = Math.floor(Math.random() * result.length);
		list.indexOf(i) === -1 && list.push(i);
	}
	const sec = encode(list.join('&'));
	const resultList = result.split('');
	const passList = pass.split('');
	for (let i = 0; i < passList.length; i++) {
		const s = passList[i]
		const index = list[i];
		resultList.splice(index, 0, s);
	}
	const t = Math.floor(Math.random() * 10);
	const l = encode(t + '' + sec.length);
	resultList.splice(t, 0, sec);
	resultList.splice(sec.length, 0, 'O' + l.length + l);
	const r = resultList.join('');
	return encode(r);
}
export const xdecode = function(encodeString) {
	const r = byteToString(decode(encodeString))
	const rList = r.split('');
	const index = rList.findIndex(i => i === 'O');
	if (index === -1) {
		return '';
	}
	const le = parseInt(rList[index + 1]);
	rList.splice(index,2)
	const rt = rList.splice(index, le).join('');
	const rtR = decode(rt);
	const rindex = parseInt(rtR[0]);
	const lenght = parseInt(rtR.substring(1, rtR.length))
	const rtn = rList.splice(rindex, lenght).join('');
	const rtnList = decode(rtn).split('&').reverse();
	while (rtnList.length > 0) {
		const idx = parseInt(rtnList.splice(0, 1));
		rList.splice(idx, 1);
	}
	return decode(rList.join(''));
}